<template lang="html">
  <div class="container">
      <!--  -->
      <my-header/>
      <div class="main">
          <!--  -->
          <bread @refresh="refresh"  :addBtn="false">
          </bread>
          <div class="main-wrapp">
              <div class="main-list">
                  <list
                    menuTitle=""
                    :menuList="menuList"
                    label="name"
                    @menuSelect="menuSelect"
                  ></list>
              </div>
              <div class="main-table">
              <div class="table-group">
                  <!--  -->
                  <search @searchEvent="searchEvent" :searchOptions="options">
                      <div  slot="search-right" class="search-button-group">
                            <el-radio-group v-model="radio" class="button-group-left" @change="radioChange" size="small">
                              <el-radio-button v-for="item in levelList" :label="item.name" :key="item.id"></el-radio-button>
                           </el-radio-group>
                           <el-dropdown trigger="click" @command="moreActions">
                               <span class="el-dropdown-link">
                                 {{moreActionsText}}<i class="el-icon-arrow-down el-icon--right"></i>
                               </span>
                               <el-dropdown-menu slot="dropdown">
                                 <el-dropdown-item command="0" v-if="moreActionsText!=''">
                                 </el-dropdown-item>
                                 <el-dropdown-item command="1"></el-dropdown-item>
                                 <el-dropdown-item command="2"></el-dropdown-item>
                                 <el-dropdown-item command="3"></el-dropdown-item>
                                 <el-dropdown-item command="4"></el-dropdown-item>
                                 <el-dropdown-item command="5"></el-dropdown-item>
                               </el-dropdown-menu>
                           </el-dropdown>
                      </div>
                  </search>
                  <div class="table-wrapp" id="tableWrapp">
                      <!--  -->
                      <el-table
                      v-loading="loading"
                      :data="tableData"
                      style="width: 100%"
                      height="100%"
                      ref="multipleTable"
                      stripe
                      border
                      header-row-class-name="table-th"
                      @row-click="rowClick"
                      @row-dblclick="rowDblclick"
                      @selection-change="selectionChange"
                      >
                      <el-table-column
                      type="selection"
                      align="center"
                      width="55">
                       </el-table-column>
                       <el-table-column
                          type="index"
                          width="80"
                          label=""
                          align="center"
                          fixed
                          >
                        </el-table-column>
                        <el-table-column
                        prop="type"
                        :formatter="formatter"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="100">
                      </el-table-column>
                      <el-table-column
                        prop="code"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="100">
                      </el-table-column>
                      <el-table-column
                        prop="difficulty"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="100">
                      </el-table-column>
                      <el-table-column
                        prop="score"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="100">
                      </el-table-column>
                      <el-table-column
                        prop="time"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="100">
                      </el-table-column>
                      <el-table-column
                        prop="content"
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="300">
                      </el-table-column>
                      <el-table-column
                        label=""
                        show-overflow-tooltip
                        tooltip-effect="light"
                        align="center"
                        min-width="120">
                        <template slot-scope="scope">
                            <div class="text-dec-link" @click="infoView(scope.row)">
                                
                            </div>
                        </template>
                      </el-table-column>
                      <el-table-column
                          fixed="right"
                          label=""
                          align="center"
                          min-width="80">
                          <template slot-scope="scope">
                            <el-button @click="switchStatus(scope.row)" type="text" size="small"><el-tag :type="scope.row.status?'danger':'success'">{{scope.row.status?"":""}}</el-tag></el-button>
                          </template>
                        </el-table-column>
                    </el-table>
                  </div>
                  <!--  -->
                  <table-footer
                      :total="count"
                      :multipleSelection="multipleSelection"
                      :deleteBtn="false"
                      @handleSizeChange="handleSizeChange"
                      @handleCurrentChange="handleCurrentChange"
                  ></table-footer>
              </div>
          </div>
        </div>
      </div>

      <dialog-box ref="dialog" title="">
          <div class="info-wrapp" slot="dialog-body">
             <table width="100%" border="0" cellspacing="0" cellpadding="0" class="my-table">
                <tr>
                    <td></td>
                    <td>{{quesTypeList[questionDetail.type]}}</td>
                    <td></td>
                    <td>{{questionDetail.code}}</td>
                </tr>

                <tr>
                    <td>&emsp;&emsp;</td>
                    <td>{{questionDetail.skillLevel}}</td>
                    <td>&emsp;&emsp;</td>
                    <td>{{questionDetail.difficulty}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{questionDetail.score}}</td>
                    <td></td>
                    <td>{{questionDetail.time}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="3">{{questionDetail.skillItem}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="3">
                        <div style="text-align:left">
                            <div v-html="questionDetail.content">
                            </div>
                            <div class="testOptions" v-if="questionDetail.type!=5">
                                <p v-for="(it,ind) in questionDetail.options" v-html=" '<span> '+letterArr[ind] +'</span>'+ it.content"></p>
                            </div>
                            <div class="testOptions" v-if="questionDetail.type==5">
                                <p v-for="(it,ind) in questionDetail.calculations" v-if="it.type=='x'">
                                    {{it.var}}={{it.val}} ({{it.scheme==1?""+it.maxValue+ " "+it.minValue+" "+it.decimalPlaces : " "+it.alternative}})
                                </p>
                            </div>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td>&emsp;&emsp;</td>
                    <td colspan="3">
                        <div style="text-align:left" v-if="questionDetail.type!=5">
                            {{questionDetail.answer}}
                        </div>
                        <div style="text-align:left" v-if="questionDetail.type==5" v-for="(it,ind) in questionDetail.calculations">
                            <div v-if="it.type=='y'">
                                {{it.formula}}={{it.val}}
                            </div>
                        </div>

                    </td>
                </tr>
             </table>
              <!-- <ul>
                  <li><span></span><span></span></li>
                  <li><span></span><span>23232323</span></li>
                  <li><span></span><span></span></li>
                  <li><span></span><span></span></li>
                  <li><span></span><span>1</span></li>
                  <li><span></span><span>1</span></li>
                  <li><span></span><span></span></li>
                  <li><span></span><span>A</span></li>
              </ul> -->
          </div>
      </dialog-box>
  </div>
</template>

<script>
import MyHeader from "../base/header"
import Bread from "../base/bread"
import Search from "../base/search"
import List from "../base/list"
import TableFooter from "../base/tableFooter"
import Append from "../base/append"
import DialogBox from "../base/dialog"
import { questionMenu, questionAll, questionOff, questionOn, questionDetail} from "@/api/getData"
import { messageText } from "@/assets/js/message"
export default {
    data() {
        return {
            options: [{
              value: 'code',
              label: ''
            }, {
              value: 'content',
              label: ''
            }, {
              value: 'difficulty',
              label: ''
            }, {
              value: 'score',
              label: ''
            }, {
              value: 'time',
              label: ''
            }],
            quesTypeList: ["","","","","",""],
            questionDetail: {},
            letterArr: ["A","B","C","D","E","F"],
            // 
            menuList: [],
            levelList: [],
            pageSize: 20,
            pageNow: 1,
            tableData: [],
            multipleSelection: [],
            loading: false,
            count: 0,
            moreActionsText: "",
            // 
            radio: "",
            // id
            currentMenu: 0,
            // 
            currentQuesType: 0,
            query: "",
            queryValue: "",
        }
    },
    created() {
        this.getTree();
    },
    computed: {
        getDataQuery() {
            var obj = {};
            if(this.currentMenu != 0) {
                obj.skillItem = this.currentMenu
            }
            if(this.currentQuesType&&this.currentQuesType!=0) {
                obj.type = this.currentQuesType;
            }
            obj.skillLevel = this.radio;
            if(this.query) {
                obj[this.query] = this.queryValue
            }
            console.log(obj);
            return obj
        }
    },
    components: {
        MyHeader,
        Bread,
        Search,
        TableFooter,
        Append,
        List,
        DialogBox
        // TableGroup
    },
    methods: {
        getTree() {
            questionMenu().then((data)=> {
                this.menuList = this.substringSkill(data.data.skill);
                this.levelList = data.data.level;
                this.getData();
            });
        },
        getData() {
            var _this = this
            this.loading = true;
            questionAll({
                appendUrl: "/"+_this.pageNow+"/"+_this.pageSize,
                param: _this.getDataQuery,
            }).then((data)=> {
                this.tableData = data.data.testList;
                this.count = data.data.count
                // this.tableData = data.data.data;
                // this.count = data.data.count;
                this.loading = false;
            })
        },
        substringSkill(data) {
            data.map((item)=> {
                item.name = item.name.substring(0,item.name.indexOf("(")).trim()
            })
            return data
        },
        // messageBox
        messageBox(data,state) {

			var that = this;
            var message = ""+state+", ?"
			messageText(this, {
				message:message,
				beforeClose(action, instance, done) {
                    if(state == "") {
                        console.log(data);
                        questionOff({
                            appendUrl: "/"+data,
                        }).then((data)=> {
							if(data.data.success) {
                                that.$message({
            						type: 'success',
            						message: state+""
            					});
								that.getData()
							} else {

							}
							done();
							instance.confirmButtonLoading = false;
                        })
                    } else {
                        questionOn({
                            appendUrl: "/"+data,
                        }).then((data)=> {
							if(data.data.success) {
                                that.$message({
            						type: 'success',
            						message: state+""
            					});
								that.getData()
							} else {

							}
							done();
							instance.confirmButtonLoading = false;
                        })
                    }
				}

			})
		},
        // 
        rowClick(row, event, column) {
            if(column.type == "selection") {
                this.$refs.multipleTable.toggleRowSelection(row)
            } else {
                this.$refs.multipleTable.clearSelection();
                this.$refs.multipleTable.toggleRowSelection(row)
            }

        },
        rowDblclick(row) {
            this.$refs.dialog.open();
            this.questionDetail = {};
            questionDetail({
                appendUrl: "/"+row.id
            }).then((data)=> {
                this.questionDetail = this.filterTest(data.data.test);
                this.$refs.dialog.loading = false;
            })
        },
        // 
        selectionChange(selections) {
            this.multipleSelection = selections;
        },
        // 
        handleSizeChange(val) {
            this.pageSize = val;
            this.getData()
        },
        handleCurrentChange(val) {
            this.pageNow = val
            this.getData()
        },
        //
        searchEvent(data) {
            this.query = data.selectValue;
            this.queryValue = data.inputValue;
            this.getData()
        },
        // 
        refresh() {
            this.getData();
        },
        // 
        menuSelect(val) {
            this.currentMenu = val;
            this.getData()
        },
        // 
        radioChange(val) {
            this.radio = val;
            this.getData()
        },
        // 
        moreActions(val) {
            this.moreActionsText = this.quesTypeList[val];
            this.currentQuesType = val;
            this.getData()
        },
        formatter(row) {
            return this.quesTypeList[row.type]
        },
        formatterInfo() {
            return ""
        },
        switchStatus(row) {
            var stateText = row.status == 1 ? "": ""
			this.messageBox(row.id, stateText)
        },
        filterTest(data) {
            if(data.type == 5) {

            } else {
                var answer = "";
                data.options.map((item,index)=> {
                    if(item.isAnswer) {
                        answer += this.letterArr[index]
                    }
                })
                data.answer = answer
            }
            return data
        },
        // 
        infoView(row) {
            this.$refs.dialog.open();
            this.questionDetail = {};
            questionDetail({
                appendUrl: "/"+row.id
            }).then((data)=> {
                this.questionDetail = this.filterTest(data.data.test);
                this.$refs.dialog.loading = false;
            })
        }
    }
}
</script>

<style lang="css">
    .search-button-group {
        display: flex;
        align-items: center;
        height: 40px;
    }
    .search-button-group .el-dropdown {
        width: 100px;
        text-align: right;
    }
    .button-group-left {
        /* margin-right: 30px */
    }
    .info-wrapp ul {
        display: flex;
        flex-wrap: wrap;
    }
    .info-wrapp ul li {
        flex: 1 1 50%;

    }
    .info-wrapp ul li>span {
        line-height: 32px;
        height: 32px;
    }
    .info-wrapp ul li span:first-of-type {
        margin-right: 20px;
    }
    .my-table {
        border-collapse: collapse;
        table-layout: fixed;
    }
    .my-table td {
        border: 1px solid #ebeef5;
        height: 32px;
        padding: 5px 10px;
        text-align: center;
    }
    .my-table td img {
        width: 120px;
        display: block;
        margin: auto;
    }
    .testOptions {
        margin-top: 10px;
    }
    .info-wrapp {
        height: 500px;
    }
</style>



