import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
// 
import Login from '@/components/login/login'

// 
import Home from '@/components/home/home'

// 
import Account from '@/components/account/account'
import Authstr from '@/components/account/authstr'
// import Audited from '@/components/account/audited'
import Prepaid from '@/components/account/prepaid'
import Learning from '@/components/account/learnsituation'
// 
import QuesBank from '@/components/quesBank/quesBank'
// 
import Info from '@/components/information/information'
// 
import Attachment from '@/components/attachment/attachment'
import Skillnotice from '@/components/attachment/skillnotice'
import Laws from '@/components/attachment/laws'
import Standard from '@/components/attachment/standard'
// 
import System from '@/components/system/system'
import Dictionary from '@/components/system/dictionary'
import Admin from '@/components/system/administrator'

Vue.use(Router)

const router = new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/login'
    },
    {
        path: '/home',
        name: 'Home',
        component: Home,
        meta: {
            requireAuth: true
        },
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
        path: '/account',
        meta: {
            name: '',
            requireAuth: true
        },
        component: Account,
        children: [
            {
                path: '',
                redirect: 'authstr'
            },
            {
                path: 'authstr',
                name: 'Authstr',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Authstr
            },

            {
                path: 'prepaid',
                name: 'Prepaid',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Prepaid
            },
            {
                path: 'learning',
                name: 'Learning',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Learning
            },
        ]
    },
    {
      path: '/quesBank',
      name: 'QuesBank',
      meta: {
          name: "",
          requireAuth: true
      },
      component: QuesBank
    },
    {
      path: '/information',
      name: 'Info',
      meta: {
          name: "",
          requireAuth: true
      },
      component: Info
    },
    {
        path: '/attachment',
        meta: {
            name: '',
            requireAuth: true
        },
        component:Attachment,
        children: [
            {
                path: '',
                redirect: 'skillnotice'
            },
            {
                path: 'skillnotice',
                name: 'Skillnotice',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Skillnotice
            },
            {
                path: 'laws',
                name: 'Laws',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Laws
            },
            {
                path: 'standard',
                name: 'Standard',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Standard
            }
        ]
    },
    {
        path: '/system',
        meta: {
            name: '',
            requireAuth: true
        },
        component:System,
        children: [
            {
                path: '',
                redirect: 'dictionary'
            },
            {
                path: 'dictionary',
                name: 'Dictionary',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Dictionary
            },
            {
                path: 'administrator',
                name: 'Admin',
                meta: {
                    name: '',
                    requireAuth: true
                },
                component: Admin
            }
        ]
    }

  ]
})
router.beforeEach((to, from, next) => {

    if (to.meta.requireAuth) {  // 
        if (store.state.session_key) {  // vuex statetoken
            next();
        }
        else {
            next({
                path: '/login',
                query: {redirect: to.fullPath}  // path
            })
        }
    }
    else {
        next();
    }
})

export default router



